@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap);

.header{max-width:1200px;margin:auto;font-family:'Roboto Condensed', sans-serif;color:#fff}.header__wrap{width:100%;padding:50px 10px;background:linear-gradient(135deg, #005585, #FF4D61, #4DBEFF, #00A2FF, #265F80);background-size:500% 100%;-webkit-animation:Gradient 30s ease infinite;animation:Gradient 30s ease infinite}.header__nav{display:flex;justify-content:flex-end;margin-bottom:60px}.header__info{display:flex;flex-direction:column;align-items:center}.header__title{margin:20px 0;padding:0;font-size:50px;line-height:68px;font-weight:900}.header__desc{margin:20px 0;padding:0;font-size:30px;line-height:32px;font-weight:normal}.nav__link{position:relative;margin-right:15px;font-family:'Roboto Condensed', sans-serif;font-size:24px;line-height:30px;color:#fff;text-decoration:none;text-transform:uppercase;-webkit-transition-duration:0.3s;transition-duration:0.3s;-webkit-transition-property:left, width, color;transition-property:left, width, color;-webkit-transition-timing-function:ease;transition-timing-function:ease}.nav__link:last-child{margin-right:0}.nav__link::before{content:'';position:absolute;left:50%;bottom:0;width:0;height:2px;background:#fff;-webkit-transition-duration:0.3s;transition-duration:0.3s;-webkit-transition-property:left, width, color;transition-property:left, width, color;-webkit-transition-timing-function:ease;transition-timing-function:ease}.nav__link:hover,.nav__link:focus{color:#0f1c20;-webkit-transition-duration:.8s;transition-duration:.8s;-webkit-transition-property:color;transition-property:color;-webkit-transition-timing-function:ease;transition-timing-function:ease}.nav__link:hover::before,.nav__link:focus::before{left:0;width:100%;background:#0f1c20;-webkit-transition-duration:0.5s;transition-duration:0.5s;-webkit-transition-property:left, width, color;transition-property:left, width, color;-webkit-transition-timing-function:ease;transition-timing-function:ease}@-webkit-keyframes Gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes Gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}.social{display:flex}.social__icon{display:block;width:40px;height:40px;margin-right:20px;background-position:0 0;background-repeat:no-repeat;background-size:cover;-webkit-transition-duration:.1s;transition-duration:.1s;-webkit-transition-property:-webkit-transform;transition-property:-webkit-transform;transition-property:transform;transition-property:transform, -webkit-transform;-webkit-transition-timing-function:ease-in;transition-timing-function:ease-in}.social__icon:last-child{margin-right:0}.social__icon:hover,.social__icon:focus{-webkit-transform:scale(1.2);transform:scale(1.2);-webkit-transition-duration:.2s;transition-duration:.2s;-webkit-transition-property:-webkit-transform;transition-property:-webkit-transform;transition-property:transform;transition-property:transform, -webkit-transform;-webkit-transition-timing-function:ease-in;transition-timing-function:ease-in}.social__github{background-image:url(../../static/media/icon-github.e4eff350.svg)}.social__telegram{background-image:url(data:image/png;base64,)}.social__codewars{background-image:url(../../static/media/icon-codewars.7228d601.svg)}.social__email{background-image:url(../../static/media/icon-email.9bd50a7c.svg)}.social__hidden{display:none}

.footer{max-width:1200px;margin:auto;padding:50px}.footer__wrap{background:linear-gradient(135deg, #005585, #FF4D61, #4DBEFF, #00A2FF, #265F80);background-size:500% 100%;-webkit-animation:Gradient 30s ease infinite;animation:Gradient 30s ease infinite}.footer__social{display:flex;justify-content:center}.footer__year{text-align:right}@-webkit-keyframes Gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes Gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}

body{margin:0;padding:0;font-family:'Roboto Condensed', sans-serif}

.project{position:relative;width:400px;height:300px;overflow:hidden}.project__title{position:absolute;top:0;left:0;margin:30px 15px;font-size:30px;line-height:36px;font-weight:bold;color:#0f1c20;z-index:2;-webkit-transition-duration:.8s;transition-duration:.8s;-webkit-transition-property:margin-top;transition-property:margin-top;-webkit-transition-timing-function:ease;transition-timing-function:ease}.project__prop{font-size:20px;line-height:28px;font-weight:normal;color:#0f1c20}.project__props{position:absolute;top:100%;left:0;right:0;height:100%;margin:0;padding:60px 30px 10px 30px;background-color:rgba(255,255,255,0.8);opacity:0;-webkit-transition-duration:2s;transition-duration:2s;-webkit-transition-property:top, opacity;transition-property:top, opacity;-webkit-transition-timing-function:ease;transition-timing-function:ease}.project__props--list{margin:0;padding:0}.project__link{font-size:20px;line-height:24px;font-weight:normal;color:#0f1c20;text-decoration:none;border-bottom:1px solid #0f1c20}.project__links{margin:0;margin-top:10px;padding:0}.project__img{width:400px;height:300px;opacity:0.8;-webkit-transition-duration:0.5s;transition-duration:0.5s;-webkit-transition-property:opacity;transition-property:opacity;-webkit-transition-timing-function:ease;transition-timing-function:ease}.project__img:hover,.project__img:focus{opacity:1;-webkit-transition-duration:0.5s;transition-duration:0.5s;-webkit-transition-property:opacity;transition-property:opacity;-webkit-transition-timing-function:ease;transition-timing-function:ease}.project:hover,.project:focus{-webkit-transition-duration:2s;transition-duration:2s;-webkit-transition-property:display;transition-property:display;-webkit-transition-timing-function:ease;transition-timing-function:ease}.project:hover .project__img,.project:hover .project__props,.project:focus .project__img,.project:focus .project__props{opacity:1;-webkit-transition-duration:.8s;transition-duration:.8s;-webkit-transition-property:opacity;transition-property:opacity;-webkit-transition-timing-function:ease;transition-timing-function:ease}.project:hover .project__props,.project:focus .project__props{top:0;-webkit-transition-duration:.8s;transition-duration:.8s;-webkit-transition-property:top;transition-property:top;-webkit-transition-timing-function:ease;transition-timing-function:ease}.project:hover .project__title,.project:focus .project__title{margin-top:20px;-webkit-transition-duration:.8s;transition-duration:.8s;-webkit-transition-property:margin-top;transition-property:margin-top;-webkit-transition-timing-function:ease;transition-timing-function:ease}.projects{display:flex;flex-wrap:wrap;justify-content:center;max-width:1200px;margin:auto}.projects__wrap{width:100%}picture{height:0;margin:0;padding:0}


.error__msg{display:flex;flex-direction:column;align-items:center;font-family:'Roboto Condensed', sans-serif;font-size:40px;line-height:45px;text-align:center;color:#743232}.error__back{display:block;margin:15px;padding:15px;text-decoration:none;color:#743232;border:2px solid #743232;-webkit-transition-duration:0.5s;transition-duration:0.5s;-webkit-transition-property:background-color, color;transition-property:background-color, color;-webkit-transition-timing-function:ease;transition-timing-function:ease}.error__back:hover,.error__back:focus{color:#fff;background-color:#743232;-webkit-transition-duration:0.5s;transition-duration:0.5s;-webkit-transition-property:background-color, color;transition-property:background-color, color;-webkit-transition-timing-function:ease;transition-timing-function:ease}

